
export class Product {
  public name: string;
  public color: string;
  public material: string;
  public imagePath: string;
  public price: number;
  public size: number;

  constructor(name: string, color: string, material: string, imagePath: string, price: number, size: number)
  {
    this.name = name;
    this.color = color;
    this.material = material;
    this.imagePath = imagePath;
    this.price = price;
    this.size = size;
  }
}
